/**
 *  
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="">
        <el-select size="small" v-model="formInline.state" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value="1"></el-option>
          <el-option label="" value="0"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="formInline.uname" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="formInline.phoneNumber" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()"></el-button>
      </el-form-item>
    </el-form>
    <!---->
    <el-table size="small" @selection-change="selectChange" :data="userData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
      <el-table-column align="center" type="index" width="50">
      </el-table-column>
      <el-table-column align="center" sortable prop="deptName" label="" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="uname" label="" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="realName" label="" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="phoneNumber" label="" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="sex" label="" min-width="80">
      </el-table-column>
      <el-table-column align="center" sortable prop="email" label="" min-width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="updatedTime" label="" min-width="120">
        <template slot-scope="scope">
          <div>{{scope.row.updatedTime|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column align="center" sortable prop="state" label="" min-width="80">
        <template slot-scope="scope">
          <el-switch v-model="scope.row.state=='1'?nshow:fshow" active-color="#13ce66" inactive-color="#ff4949" @change="editType(scope.$index, scope.row)">
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="" min-width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="success" @click="resetpwd(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
    <!--  -->
    <el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click='closeDialog("edit")'>
      <el-form label-width="80px" ref="editForm" :model="editForm" :rules="rules">
        <el-form-item label="" prop="uname">
          <el-input size="small" v-model="editForm.uname" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="realName">
          <el-input size="small" v-model="editForm.realName" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="deptId">
          <el-select size="small" v-model="editForm.deptId" placeholder="" class="userDept">
            <el-option v-for="item in deptList" :key="item.deptId" :label="item.deptName"  :value="item.deptId"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="roleId">
          <el-select size="small" v-model="editForm.roleId" placeholder="" class="userRole">
            <el-option label="" value="686a54140cfa4cf0b0c2ad51755a3d68"></el-option>
            <el-option label="" value="045001730c5c4c60a16b35fdb7aaae4b"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="phoneNumber">
          <el-input size="small" v-model="editForm.phoneNumber" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input size="small" v-model="editForm.email" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="sex">
          <el-radio v-model="editForm.sex" label=""></el-radio>
          <el-radio v-model="editForm.sex" label=""></el-radio>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click='closeDialog("edit")'></el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')"></el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
// 
import {
  userList,
  getDeptList,
  userSave,
  userDelete,
  userPwd,
  userExpireToken,
  userFlashCache,
  userState,
  UserDeptTree,
  UserDeptSave,
  UserDeptdeptTree,
  UserChangeDept
} from '../../api/userMG'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      title: '',
      editFormVisible: false, //
      dataAccessshow: false, //
      unitAccessshow: false, //
      // 
      editForm: {
        uid: '',
        uname: '',
        realName: '',
        deptId: '',
        roleId: '',
        phoneNumber: '',
        email: '',
        sex: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      // 
      selectdata: [],
      // rules
      rules: {
        uname: [
          { required: true, message: '', trigger: 'blur' }
        ],
        realName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        roleId: [{ required: true, message: '', trigger: 'blur' }],
        deptId: [{ required: true, message: '', trigger: 'blur' }],
        phoneNumber: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^1(3\d|47|5((?!4)\d)|7(0|1|[6-8])|8\d)\d{8,8}$/,
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/,
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        sex: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      seletedata: {
        uid: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      // 
      resetpsd: {
        uid: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      // 
      getdeptListparam: {
        usertoken: sessionStorage.getItem('logintoken')
      },
      // 
      formInline: {
        pageNum: 1,
        pageSize: 10,
        usertoken: sessionStorage.getItem('logintoken'),
        uname: '',
        phoneNumber: '',
        state: ''
      },
      //
      userData: [],
      //
      deptList: [],
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },

  /**
   * 
   */
  watch: {},

  /**
   * 
   */
  created() {
    this.getdata(this.formInline)
    this.getDeptSelectList(this.getdeptListparam)
  },

  /**
   * 
   */
  methods: {
    // 
    getdata(parameter) {
      this.loading = true
      /***
       *  
       */
      //
      userList(parameter).then(res => {
        this.loading = false
        if (res.code != 200) {
          this.$message({
            type: 'info',
            message: res.data
          })
        } else {
          this.userData = res.data.list
          console.log(this.userData)
          // 
          this.pageparm.currentPage = this.formInline.pageNum
          this.pageparm.pageSize = this.formInline.pageSize
          this.pageparm.total = res.data.total
        }
      })
    },
    //
    getDeptSelectList(paramer){
      getDeptList(paramer).then(res => {
          if(res.code == 200){
            this.deptList = res.data;
            console.log(this.deptList)
          }else{
            this.$message.error(res.data);
          }
      })
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.limit = parm.pageSize
      this.getdata(this.formInline)
    },
    //
    search() {
      this.getdata(this.formInline)
    },
    // 
    editType: function(index, row) {
      this.loading = true
      let parm = {
        state: '',
        uid: '',
        usertoken: sessionStorage.getItem('logintoken')
      }
      parm.uid = row.uid
      let state = row.state
      if (state == '1') {
        parm.state = '0'
      } else {
        parm.state = '1'
      }
      // 
      userState(parm).then(res => {
        this.loading = false
        if (res.code != 200) {
          this.$message({
            type: 'info',
            message: res.data
          })
        } else {
          this.$message({
            type: 'success',
            message: ''
          })
          this.getdata(this.formInline)
        }
      })
    },
    //
    handleEdit: function(index, row) {
      this.editFormVisible = true
      if (row != undefined && row != 'undefined') {
        this.title = ''
        this.editForm.uid = row.uid
        this.editForm.uname = row.uname
        this.editForm.realName = row.realName
        this.editForm.roleId = row.roleId
        this.editForm.deptId = row.deptId
        this.editForm.phoneNumber = row.phoneNumber
        this.editForm.email = row.email
        this.editForm.sex = row.sex
      } else {
        this.title = ''
        this.editForm.uid = ''
        this.editForm.uname = ''
        this.editForm.realName = ''
        this.editForm.roleId = ''
        this.editForm.deptId = ''
        this.editForm.phoneNumber = ''
        this.editForm.email = ''
        this.editForm.sex = ''
      }
    },
    // 
    submitForm(editData) {
      this.$refs[editData].validate(valid => {
        if (valid) {
          // 
          console.log(this.editForm)
          userSave(this.editForm)
            .then(res => {
              this.editFormVisible = false
              this.loading = false
              if (res.code == 200) {
                this.getdata(this.formInline)
                this.$message({
                  type: 'success',
                  message: ''
                })
              } else {
                this.$message({
                  type: 'info',
                  message: res.data
                })
              }
            })
            .catch(err => {
              this.editFormVisible = false
              this.loading = false
              this.$message.error('')
            })
        } else {
          return false
        }
      })
    },
    // 
    selectChange(val) {
      this.selectdata = val
    },
    // 
    closeDialog(dialog) {
      if (dialog == 'edit') {
        this.editFormVisible = false
      } else if (dialog == 'perm') {
        this.dataAccessshow = false
      } else if (dialog == 'unit') {
        this.unitAccessshow = false
      }
    },
    // 
    deleteUser(index, row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          // 
          this.seletedata.uid = row.uid;
          userDelete(this.seletedata)
            .then(res => {
              if (res.code == 200) {
                this.$message({
                  type: 'success',
                  message: '!'
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.data
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // 
    resetpwd(index, row) {
      this.resetpsd.uid = row.uid
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          userPwd(this.resetpsd)
            .then(res => {
              if (res.code == 200) {
                this.$message({
                  type: 'success',
                  message: ''
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.data
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.userDept {
  width: 100%;
}
</style>

 


