/** 
* 
*/

<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="" >
        <el-select size="small" v-model="formInline.processTypeId" placeholder="" clearable="">
          <el-option v-for="item in this.processTypeList" :label="item.typename" :value="item.id" :key="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleCreateBeginNode()"></el-button>
      </el-form-item>
    </el-form>
    <!---->
    <el-table size="small" @expand-change="expandChange" :data="nodeTypeData" highlight-current-row v-loading="loading"  element-loading-text="" style="width: 100%;">
      <el-table-column align="center" type="expand" width="50">
        <template slot-scope="props">
        <el-table  size="small" label-position="left" :data="nodeTypeListWithQueue" highlight-current-row v-loading="loading" element-loading-text="">
          <el-table-column align="center" sortable prop="name" label="" width="280">
          </el-table-column>
          <el-table-column align="center" sortable prop="correlationName" label="" width="200">
          </el-table-column>
          <el-table-column align="center" sortable prop="parentsName" label="" width="380">
          </el-table-column>
          <el-table-column label="" min-width="200">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="danger" @click="deleteNodeType(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
      </el-table-column>
      <el-table-column align="center" sortable prop="name" label="" width="280">
      </el-table-column>
      <el-table-column align="center" sortable prop="correlationName" label="" width="200">
      </el-table-column>
      <el-table-column align="center" sortable prop="parentsName" label="" width="380">
      </el-table-column>
      <el-table-column label="" min-width="200">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="deleteNodeType(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
    <!--  -->
    <el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click='closeDialog("edit")'>
      <el-form label-width="120px" ref="editForm" :model="editForm" :rules="rules">
        <el-form-item label="" required>
        <el-select size="small" v-model="editForm.processTypeId" placeholder="">
          <el-option v-for="item in this.processTypeList" :label="item.typename" :value="item.id" :key="item.id"></el-option>
        </el-select>
      </el-form-item>
        <el-form-item label="" prop="name" required>
          <el-input size="small" v-model="editForm.name" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="isBeginNode" required>
          <el-select size="small" v-model="editForm.isBeginNode" placeholder="" class="userRole">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="isEndNode" required>
          <el-select size="small" v-model="editForm.isEndNode" placeholder="" class="userRole">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="parentsId" v-if="editForm.isBeginNode == '0'" required>
          <el-select size="small" v-model="editForm.parentsId" multiple placeholder="" class="userRole">
            <el-option v-for="item in nodeTypeListWithQueue" :label="item.name" :value="item.id" :key="item.id"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label=""  prop="correlationId">
          <el-select size="small" v-model="editForm.correlationId" placeholder="" class="userRole">
            <el-option v-for="item in userData" :label="item.realName" :value="item.uid" :key="item.uid"></el-option>
          </el-select>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click='closeDialog("edit")'></el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')"></el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
// 
import { getProcessTypeList, getProcessNodeTypeBeginList ,getNodeTypeListByQueue, postNodeType} from '../../api/processMG'
import { userList } from '../../api/userMG'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      title: '',
      editFormVisible: false, //
      dataAccessshow: false, //
      unitAccessshow: false, //
      // 
      editForm: {
        id: '',
        name: '',
        parentsId: '',
        isBeginNode: '1',
        isEndNode: '',
        correlationId: '',
        processTypeId: '',
        nodeTypeQueue: '',
        parentsName: '',
        correlationName: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      //
       deleteForm: {
        id: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      // rules
      rules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        processTypeId: [{ required: true, message: '', trigger: 'blur' }],
        isBeginNode: [{ required: true, message: '', trigger: 'blur' }],
        isEndNode: [{ required: true, message: '', trigger: 'blur' }],
        parentsId: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      getUserParams: {
        pageNum: 1,
        pageSize: 10000,
        usertoken: sessionStorage.getItem('logintoken'),
        state: '1'
      },
      //usertoken 
      usertokenParamer: {
        usertoken: sessionStorage.getItem('logintoken')
      },
      // 
      formInline: {
        pageNum: 1,
        pageSize: 10,
        usertoken: sessionStorage.getItem('logintoken'),
        processTypeId: ''
      },
      //queue
      formInlineWithQueue: {
        usertoken: sessionStorage.getItem('logintoken'),
        nodeTypeQueue: ''
      },
      //
      userData: [],
      //
      nodeTypeData: [],
      //
      processTypeList: [],
      //queue
      nodeTypeListWithQueue: [],
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },

  /**
   * 
   */
  watch: {},

  /**
   * 
   */
  created() {
    this.getSelectNodeTypeList(this.usertokenParamer)
    this.getUser(this.getUserParams)
    this.getdata(this.formInline)
  },

  /**
   * 
   */
  methods: {
    // 
    getdata(parameter) {
      this.loading = true
      /***
       *  
       */
      //
      getProcessNodeTypeBeginList(parameter).then(res => {
        this.loading = false
        if (res.code != 200) {
          this.$message({
            type: 'info',
            message: res.data
          })
        } else {
          this.nodeTypeData = res.data.list
          // 
          this.pageparm.currentPage = this.formInline.pageNum
          this.pageparm.pageSize = this.formInline.pageSize
          this.pageparm.total = res.data.total
        }
      })
    },
    //
    getSelectNodeTypeList(paramer){
      getProcessTypeList(paramer).then(res => {
          if(res.code == 200){
            this.processTypeList = res.data;
          }else{
            this.$message.error(res.data);
          }
      })
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.limit = parm.pageSize
      this.getdata(this.formInline)
    },
    //
    search() {
      this.getdata(this.formInline)
    },
    //
    handleEdit: function(index, row) {
      this.editFormVisible = true
      //
      this.getSelectNodeTypeList(this.usertokenParamer)
      if (row != undefined && row != 'undefined') {
        this.title = ''
        this.editForm.id = ""
        this.editForm.name = ""
        this.editForm.parentsId = row.parentsId
        this.editForm.parentsName = row.parentsName
        this.editForm.correlationId = row.correlationId
        this.editForm.correlationName = row.correlationName
        this.editForm.isBeginNode = ""
        this.editForm.isEndNode = ""
        this.editForm.processTypeId = row.processTypeId
        this.editForm.nodeTypeQueue = row.nodeTypeQueue
        this.formInlineWithQueue.nodeTypeQueue = row.nodeTypeQueue
        //
        this.getSelectNodeTypeList(this.usertokenParamer)
        getNodeTypeListByQueue(this.formInlineWithQueue).then(res => {
          if(res.code == 200){
            this.nodeTypeListWithQueue = res.data;
          }else{
            this.$message.error(res.data);
          }
        }).catch(err => {
          this.$message.error('')
        })
      } else {
        this.title = ''
         this.editForm.id = ''
        this.editForm.name = ''
        this.editForm.parentsId = ''
        this.editForm.parentsName = ''
        this.editForm.correlationId = ''
        this.editForm.correlationName = ''
        this.editForm.isBeginNode = '1'
        this.editForm.isEndNode = ''
        this.editForm.processTypeId = ''
        this.editForm.nodeTypeQueue = ''
      }
    },
    //
    deleteNodeType: function(index,row){
      this.deleteForm.id = row.id;
      // 
      postNodeType(this.deleteForm)
        .then(res => {
          this.editFormVisible = false
          this.loading = false
          if (res.code == 200) {
            this.getdata(this.formInline)
            this.$message({
              type: 'success',
              message: ''
            })
          } else {
            this.$message({
              type: 'info',
              message: res.data
            })
          }
        })
        .catch(err => {
          this.editFormVisible = false
          this.loading = false
          this.$message.error('')
        })
    },
    //
    handleCreateBeginNode: function(index, row) {
      this.editFormVisible = true
      //
      this.getSelectNodeTypeList(this.usertokenParamer)
      
      if (row != undefined && row != 'undefined') {
        this.title = ''
        this.editForm.id = row.id
        this.editForm.name = row.name
        this.editForm.parentsId = row.parentsId
        this.editForm.parentsName = row.parentsName
        this.editForm.correlationId = row.correlationId
        this.editForm.correlationName = row.correlationName
        this.editForm.isBeginNode = row.isBeginNode
        this.editForm.isEndNode = row.isEndNode
        this.editForm.processTypeId = row.processTypeId
        this.editForm.nodeTypeQueue = row.nodeTypeQueue
        this.formInlineWithQueue.nodeTypeQueue = row.nodeTypeQueue
        //
        this.getSelectNodeTypeList(this.usertokenParamer)
        getNodeTypeListByQueue(this.formInlineWithQueue).then(res => {
          if(res.code == 200){
            this.nodeTypeListWithQueue = res.data;
          }else{
            this.$message.error(res.data);
          }
        }).catch(err => {
          this.$message.error('')
        })
      } else {
        this.title = ''
         this.editForm.id = ''
        this.editForm.name = ''
        this.editForm.parentsId = ''
        this.editForm.parentsName = ''
        this.editForm.correlationId = ''
        this.editForm.correlationName = ''
        this.editForm.isBeginNode = '1'
        this.editForm.isEndNode = ''
        this.editForm.processTypeId = ''
        this.editForm.nodeTypeQueue = ''
      }
    },
    // 
    submitForm(editData) {
      this.$refs[editData].validate(valid => {
        if (valid) {
          // 
          postNodeType(this.editForm)
            .then(res => {
              this.editFormVisible = false
              this.loading = false
              if (res.code == 200) {
                this.getdata(this.formInline)
                this.$message({
                  type: 'success',
                  message: ''
                })
              } else {
                this.$message({
                  type: 'info',
                  message: res.data
                })
              }
            })
            .catch(err => {
              this.editFormVisible = false
              this.loading = false
              this.$message.error('')
            })
        } else {
          return false
        }
      })
    },
    // 
    expandChange(row,expandedRows) {
      this.formInlineWithQueue.nodeTypeQueue = row.nodeTypeQueue;
      getNodeTypeListByQueue(this.formInlineWithQueue).then(res => {
        if(res.code == 200){
          this.nodeTypeListWithQueue = res.data;
        }else{
          this.$message.error(res.data)
        }
      }).catch( error => {
        this.$message.error('');
      })
      
    },
    // 
    closeDialog(dialog) {
      if (dialog == 'edit') {
        this.editFormVisible = false
      } else if (dialog == 'perm') {
        this.dataAccessshow = false
      } else if (dialog == 'unit') {
        this.unitAccessshow = false
      }
    },
    // 
    getUser(paramer) {
       //
      userList(paramer).then(res => {
        if (res.code != 200) {
          this.$message({
            type: 'info',
            message: res.data
          })
        } else {
          this.userData = res.data.list
          // 
          this.pageparm.currentPage = this.formInline.pageNum
          this.pageparm.pageSize = this.formInline.pageSize
          this.pageparm.total = res.data.total
        }
      })
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.userDept {
  width: 100%;
}
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
</style>
</style>

 


