/**
 *  
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" :model="searchformInline" class="user-search">
      <el-form-item label="">
        <el-input size="small" v-model="searchformInline.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()"></el-button>
      </el-form-item>
    </el-form>
    <!---->
    <el-table size="small" @selection-change="selectChange" :data="stationData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
      <el-table-column align="center" type="index" width="50">
      </el-table-column>
      <el-table-column align="center" sortable prop="deptName" label="" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="name" label="" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="uname" label="" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="createdtime" label="" width="150">
        <template slot-scope="scope">
            <div>{{scope.row.createdtime|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column label="" min-width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="deleteStation(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
    <!--  -->
    <el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click='closeDialog("edit")'>
      <el-form label-width="80px" ref="editForm" :model="editForm" :rules="rules">
        <el-form-item label="" prop="name">
          <el-input size="small" v-model="editForm.name" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="uid">
            <el-select size="small" v-model="editForm.uid" placeholder="" class="userDept">
                <el-option v-for="item in userselectList" :key="item.uid" :label="item.realName"  :value="item.uid"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="deptid">
          <el-select size="small" v-model="editForm.deptid" placeholder="" class="userDept">
            <el-option v-for="item in deptList" :key="item.deptId" :label="item.deptName"  :value="item.deptId"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click='closeDialog("edit")'></el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')"></el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
// 
import { userList,getDeptList,getStationList,postStation } from '../../api/userMG'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      title: '',
      editFormVisible: false, //
      dataAccessshow: false, //
      unitAccessshow: false, //
      // 
      editForm: {
        id: '',
        uid: '',
        name: '',
        realName: '',
        deptid: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      // 
      selectdata: [],
      // rules
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        uid: [
          { required: true, message: '', trigger: 'blur' }
        ],
        deptid: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      seletedata: {
        id: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      // 
      getdeptListparam: {
        usertoken: sessionStorage.getItem('logintoken')
      },
      // 
      formInline: {
        pageNum: 1,
        pageSize: 10,
        usertoken: sessionStorage.getItem('logintoken'),
        name: ''
      },
      //
      searchformInline: {
        pageNum: 1,
        pageSize: 1000,
        usertoken: sessionStorage.getItem('logintoken'),
        name: ''
      },
      //
      userListParams: {
          pageNum : 1,
          pageSize : 1000,
          usertoken: sessionStorage.getItem('logintoken')
      },
      //
      userselectList: [],
      //
      stationData: [],
      //
      deptList: [],
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },

  /**
   * 
   */
  created() {
    this.getdata(this.formInline)
    this.getDeptSelectList(this.getdeptListparam)
    this.getUserList()
  },

  /**
   * 
   */
  methods: {
    // 
    getdata(parameter) {
      this.loading = true
      getStationList(parameter).then(res => {
        this.loading = false
        if (res.code != 200) {
          this.$message({
            type: 'info',
            message: res.data
          })
        } else {
          this.stationData = res.data.list
          // 
          this.pageparm.currentPage = this.formInline.pageNum
          this.pageparm.pageSize = this.formInline.pageSize
          this.pageparm.total = res.data.total
        }
      })
    },
    //
    getDeptSelectList(paramer){
      getDeptList(paramer).then(res => {
          if(res.code == 200){
            this.deptList = res.data;
          }else{
            this.$message.error(res.data);
          }
      })
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.limit = parm.pageSize
      this.getdata(this.formInline)
    },
    //
    search() {
      this.getdata(this.searchformInline)
    },
    //
    handleEdit: function(index, row) {
      this.editFormVisible = true
      if (row != undefined && row != 'undefined') {
        this.title = ''
        this.editForm.id = row.id
        this.editForm.uid = row.uid
        this.editForm.name = row.name
        this.editForm.deptid = row.deptid
      } else {
        this.title = ''
        this.editForm.id = ''
        this.editForm.uid = ''
        this.editForm.name = ''
        this.editForm.deptid = ''
      }
    },
    // 
    submitForm(editData) {
      this.$refs[editData].validate(valid => {
        if (valid) {
          // 
          postStation(this.editForm)
            .then(res => {
              this.editFormVisible = false
              this.loading = false
              if (res.code == 200) {
                this.getdata(this.formInline)
                this.$message({
                  type: 'success',
                  message: ''
                })
              } else {
                this.$message({
                  type: 'info',
                  message: res.data
                })
              }
            })
            .catch(err => {
              this.editFormVisible = false
              this.loading = false
              this.$message.error('')
            })
        } else {
          return false
        }
      })
    },
    // 
    selectChange(val) {
      this.selectdata = val
    },
    // 
    closeDialog(dialog) {
      if (dialog == 'edit') {
        this.editFormVisible = false
      } else if (dialog == 'perm') {
        this.dataAccessshow = false
      } else if (dialog == 'unit') {
        this.unitAccessshow = false
      }
    },
    // 
    deleteStation(index, row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
    }).then(() => {
        // 
        this.seletedata.id = row.id;
        postStation(this.seletedata)
        .then(res => {
            if (res.code == 200) {
            this.$message({
                type: 'success',
                message: '!'
            })
            this.getdata(this.formInline)
            } else {
            this.$message({
                type: 'info',
                message: res.data
            })
            }
        }).catch(err => {
            this.loading = false
            this.$message.error('')
        })
    }).catch(() => {
        this.$message({
            type: 'info',
            message: ''
        })
    })},
    //
    getUserList(){
         //
      userList(this.userListParams).then(res => {
        this.loading = false
        if (res.code != 200) {
          this.$message({
            type: 'info',
            message: res.data
          })
        } else {
          this.userselectList = res.data.list
        }
      })
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.userDept {
  width: 100%;
}
</style>

 


