// 
import Vue from 'vue';
import Router from 'vue-router';
// 
import login from '@/views/login';
// 
import index from '@/views/index';
/**
 * 
 */
// 
import Goods from '@/views/goods/Goods';
// 
import CreatePro from '@/views/process/CreatePro';
// 
import MyAsk from '@/views/process/MyAsk';
/**
 * 
 */
// 
import Order from '@/views/pay/Order';
/**
 * 
 */
// 
import user from '@/views/system/user';
// 
import Role from '@/views/system/Role';
// 
import Dept from '@/views/system/Dept';
//
import Station from '@/views/system/Station';
// 
import Permission from '@/views/system/Permission';

//
import NodeType from '@/views/system/NodeType';


// 
Vue.use(Router);

//  
export default new Router({
    routes: [{
        path: '/',
        name: '',
        component: login,
        hidden: true,
        meta: {
            requireAuth: false
        }
    }, {
        path: '/login',
        name: '',
        component: login,
        hidden: true,
        meta: {
            requireAuth: false
        }
    }, {
        path: '/index',
        name: '',
        component: index,
        iconCls: 'el-icon-tickets',
        children: [{
            path: '/process/todo',
            name: '',
            component: Goods,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/process/new',
            name: '',
            component: CreatePro,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/process/myask',
            name: '',
            component: MyAsk,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/process/find',
            name: '',
            component: Order,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/process/haddo',
            name: '',
            component: Order,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/system/user',
            name: '',
            component: user,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/system/Role',
            name: '',
            component: Role,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/system/Dept',
            name: '',
            component: Dept,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/system/Station',
            name: '',
            component: Station,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/system/NodeType',
            name: '',
            component: NodeType,
            meta: {
                requireAuth: true
            }
        },  {
            path: '/system/Permission',
            name: '',
            component: Permission,
            meta: {
                requireAuth: true
            }
        }]
    }]
})


